@use './mixins.scss';

@import './helper.scss';
@import './vars.scss';

main > section {
  display: block;
}

.section .content::before,
.section .content::after {
  content: ' ';
  display: table;
  clear: both;
}

.section .title {
  @include mixins.safearea;
  margin-top: 2rem;
}

.section .title h2 {
  font-size: rem( 40 );
  letter-spacing: -.02em;
}

.section .item {
  overflow: auto;
}

.section .list {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  list-style: none;
  position: relative;
  margin-top: .8rem;
  overflow: auto;
}

.section .scroll-snap {
  @supports (scroll-snap-align: center) {
    -webkit-scroll-snap-type: mandatory;
    -webkit-scroll-snap-points-x: repeat(100%);
    scroll-snap-type: x mandatory;
    scroll-snap-points-x: repeat(100%);

    > li { scroll-snap-align: center; }
  }
}

.section .devider {
  min-width: 6.4%;
  display: block;
}

.section .list-item {
  flex: 0 1 100%;
  display: block;
  overflow: hidden;
  min-width: 87.2%;
  border-radius: $card-small-border-radius;
  background-color: $color-placeholder;

  + .list-item {
    margin-left: .883rem;
  }
}

.section .fetching {
  background-color: $color-placeholder;
  padding-top: $card-small-padding-top;
  border-radius: $card-small-border-radius;
  @include mixins.safearea;
  overflow: hidden;
}

.section .fetching::before {
  @keyframes waiting {
    from { left: rem(-150); }
    to { left: 100%; }
  }

  content: '';
  display: block;
  position: absolute;
  left: rem(-150);
  top: 0;
  height: 100%;
  width: rem(150);
  background: linear-gradient(to right, transparent 0%, $color-placeholder-hints 50%, transparent 100%);
  animation: 1s cubic-bezier(0.4, 0.0, 0.2, 1) infinite waiting;
}

.section .list:not(.fetching) a:not(.active) {
  padding-top: 150%;
  display: block;
}

.section .card [data-bg-load=true] {
  background-size: contain;
  background-repeat: no-repeat;
}

.section .card-link {
  position: relative;
}

.section .card .active {
  background-size: cover;
  background-position: center;
  padding-top: 100vh;
  @include mixins.entirelyFull('fixed');
  z-index: 5;
}

.section .card-content {
  @include mixins.entirelyFull;
}